#cookies_notice
{
    position: fixed;
    bottom: 0px;
    left: 0px;
    width: 100%;
    display: block;
}

#cookies_notice > div {
    margin: 0;
    font-size: 0.8rem;
}

#cookies_notice .card-title {
    line-height: 3rem;
    font-size: 2rem;
}

#cookies_notice .card-content {
    padding: 5px;
}

#cookies_notice_close {
    border-radius: 3px;
    padding: 0 5px;
    font-size: 150%;
    display: block;
    min-height: 40px;
    line-height: 40px;
    margin: auto;
    margin-top: 5px;
    text-align: center;
}

#cookies_notice_close:hover {
    box-shadow: 2px 2px 2px #69B1E6;
}

span.tag {
    padding: 10px;
}

h1 {
    font-size: 3.6rem;
}

h2 {
    font-size: 3.2rem;
}

.post-link {
    font-size: 1.2em;
}

a.btn-share-email {
    display:block;
    text-align:center
}

.archive-item {
    font-weight: bold;
    text-decoration: underline;
    font-size: 1.6em;
    text-shadow: 1px 0px #777;
}

#footer-copy {
    text-align: center;
    margin-bottom: 0;
}
